<template>
  <div>
    <v-text-field
      :value="formattedTimeValue"
      append-icon="mdi-calendar"
      placeholder="##:##"
      @click:append="handleDialogOpen"
      :rules="[rules.required, rules.timeFormat]"
      :label="label"
      v-model="time"
      @change="onInputChange"
    ></v-text-field>
    <v-dialog v-model="showMenu" max-width="300px">
      <v-card v-if="showMenu">
        <v-card-text class="px-0 py-0">
          <v-time-picker v-model="time" full-width></v-time-picker>
        </v-card-text>
        <v-card-actions class="text-right">
          <v-btn color="grey darken-1" text @click.native="clearHandler">{{ clearText }}</v-btn>
          <v-btn color="grey lighten-1" text @click.native="cancelHandler">Cancel</v-btn>
          <v-btn color="green darken-1" text @click="okHandler">{{ okText }}</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
const DEFAULT_TIME = null;
const DEFAULT_OK_TEXT = 'Ok';
const DEFAULT_CLEAR_TEXT = 'Clear';

export default {
  name: 'v-timepicker',
  props: {
    value: {
      type: String,
      default: null,
    },
    okText: {
      type: String,
      default: DEFAULT_OK_TEXT,
    },
    clearText: {
      type: String,
      default: DEFAULT_CLEAR_TEXT,
    },
    label: {
      type: String,
    },
  },
  computed: {
    formattedTimeValue() {
      return this.value;
    },
  },
  data(vm) {
    return {
      time: vm.value,
      showMenu: false,
      rules: {
        required: value => !!value || 'Required.',
        timeFormat: value =>
          (() => {
            const timeValues = (value && value.split(':')) || [];
            if (timeValues.length >= 1) {
              if (timeValues[0] !== null && isNaN(+timeValues[0])) {
                return 'Hour should be numeric.';
              }
              if (timeValues[0] >= 24) {
                return 'Hour value should be less than 24 hours.';
              } else if (timeValues[0] < 0) {
                return 'Hour value should be greater than 0.';
              }
            }
            if (timeValues.length >= 2) {
              if (timeValues[1] !== null && isNaN(+timeValues[1])) {
                return 'Minute should be numeric.';
              }
              if (timeValues[1] < 0) {
                return 'Minute value should be greater than 0.';
              } else if (timeValues[1] >= 60) {
                return 'Minute value should be less than 60 minutes.';
              }
            }
            if (timeValues.length >= 3) {
              return 'Should be in format HH:MM';
            }
            return true;
          })(),
      },
    };
  },
  watch: {
    value: function (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.time = newVal;
      }
    },
  },
  methods: {
    onInputChange() {
      this.$emit('input', this.time);
    },
    okHandler() {
      this.showMenu = false;
      this.$emit('input', this.time);
    },
    handleDialogOpen() {
      this.showMenu = true;
    },
    clearHandler() {
      this.time = null;
      this.$emit('input', null);
    },
    cancelHandler() {
      this.showMenu = false;
    },
  },
};
</script>
