<template>
  <v-menu v-model="menu" :close-on-content-click="false" :nudge-width="200" offset-x>
    <template v-slot:activator="{ on, attrs }">
      <v-btn color="orange darken-2" class="mr-1 btn-height-22" x-small v-bind="attrs" v-on="on">
        <span>Calendar Settings</span>
        <v-icon x-small right> mdi-calendar</v-icon>
      </v-btn>
    </template>

    <v-card class="text-left">
      <v-list>
        <v-list-item>
          <v-list-item-avatar>
            <v-icon>mdi-calendar</v-icon>
          </v-list-item-avatar>

          <v-list-item-content>
            <v-list-item-title>Calendar Panel</v-list-item-title>
            <v-list-item-subtitle>Configure timezone/schedule names</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>
      <v-divider></v-divider>

      <v-card-text>
        <v-form v-model="valid" ref="form" lazy-validation>
          <v-row dense>
            <v-col cols="12">
              <v-autocomplete
                dense
                required
                clearable
                v-model="timezoneVal"
                :items="timezones"
              ></v-autocomplete>
            </v-col>
            <v-col cols="12">
              <v-divider></v-divider>
            </v-col>

            <v-col cols="12">
              <p class="caption">Schedule Names</p>
              <div class="schedule-name-list-wrapper">
                <div v-for="(scheduleName, index) in scheduleNameList" :key="`${index}-${listRenderCount}`">
                  <v-row dense>
                    <v-col cols="11">
                      <v-text-field dense :rules="[rules.required]" required v-model="scheduleName.name" />
                    </v-col>
                    <v-col cols="1">
                      <tooltip-icon-button
                        btnColor="red"
                        btnIcon="mdi-delete"
                        buttonSize="medium"
                        btnTooltip="Delete"
                        :onClick="() => removeScheduleName(scheduleName.id)"
                      />
                    </v-col>
                  </v-row>
                </div>
              </div>

              <v-col cols="12" class="text-center">
                <tooltip-icon-button
                  btnColor="blue"
                  btnIcon="mdi-plus"
                  buttonSize="medium"
                  btnTooltip="Add Schedule Name"
                  :onClick="() => createScheduleName('')"
                />
              </v-col>
            </v-col>
          </v-row>
        </v-form>
      </v-card-text>

      <v-card-actions>
        <v-spacer></v-spacer>

        <v-btn text @click="menu = false"> Cancel </v-btn>
        <v-btn color="primary" text @click="onSave"> Save </v-btn>
      </v-card-actions>
    </v-card>
  </v-menu>
</template>
<script>
import { v4 as uuidv4 } from 'uuid';
import * as timezoneUtil from '../../../utils/timezone';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';
export default {
  data: () => ({
    fav: true,
    valid: false,
    menu: false,
    listRenderCount: 0,
    message: false,
    hints: true,
    timezoneVal: null,
    timezones: [],
    names: [],
    scheduleNameList: [],
    rules: {
      required: value => !!value || 'Required.',
    },
  }),
  components: { TooltipIconButton },
  props: ['timezone', 'scheduleNames', 'onSubmit'],
  mounted() {
    this.initialize();
  },
  watch: {
    menu: function (newVal, oldVal) {
      if (newVal) {
        this.initialize();
      }
    },
  },
  methods: {
    initialize() {
      if (!this.timezone) {
        this.timezoneVal = timezoneUtil.getBrowserTimezone();
      } else {
        this.timezoneVal = this.timezone;
      }
      this.timezones = timezoneUtil.timezones();

      this.updateScheduleNameList();
    },
    updateScheduleNameList() {
      this.scheduleNameList = Object.values(this.scheduleNames || {}).map(item => ({
        ...item,
      }));
    },
    createScheduleName(name) {
      const uuid = uuidv4();
      const schedule = {
        id: uuid,
        name,
      };
      this.scheduleNameList = this.scheduleNameList.concat(schedule);
    },
    updateScheduleName(id, name) {
      this.scheduleNames = {
        ...this.scheduleNames,
        [id]: {
          ...this.scheduleNames[id],
          name,
        },
      };
    },
    removeScheduleName(id) {
      this.scheduleNameList = this.scheduleNameList.filter(val => val.id !== id);
    },
    onSave() {
      this.menu = false;
      if (!this.$refs.form.validate()) {
        return;
      }
      const payload = {
        timezone: this.timezoneVal,
        names: this.scheduleNameList.reduce((acc, curr) => {
          acc[curr.id] = {
            ...curr,
          };
          return acc;
        }, {}),
      };

      this.onSubmit(payload);
    },
  },
};
</script>
<style>
.schedule-name-list-wrapper {
  max-height: 30vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.schedule-name-list-wrapper::-webkit-scrollbar {
  width: 10px;
}

.schedule-name-list-wrapper::-webkit-scrollbar-track {
  background: #202020;
  border-left: 1px solid #2c2c2c;
}

.schedule-name-list-wrapper::-webkit-scrollbar-thumb {
  background: #3e3e3e;
  border: solid 2px #202020;
  border-radius: 6px;
}

.schedule-name-list-wrapper::-webkit-scrollbar-thumb:hover {
  background: white;
}
</style>
