import moment from 'moment-timezone';
import { generate as uuid } from 'short-uuid';

const DATE_FORMAT = 'YYYY-MM-DDTHH:mm';
const DEFAULT_SCHEDULE_NAMES = [{ name: 'HVAC' }];
export const DAY_MAP = {
  0: 'sunday',
  1: 'monday',
  2: 'tuesday',
  3: 'wednesday',
  4: 'thursday',
  5: 'friday',
  6: 'saturday',
};

export const CALENDAR_EVENT_TYPES = {
  EVENT: 'events',
  WEEKLY: 'weekly',
  EXCEPTION: 'exception',
};

export const CALENDAR_EVENT_DISPLAYNAME = {
  [CALENDAR_EVENT_TYPES.EVENT]: 'Event',
  [CALENDAR_EVENT_TYPES.WEEKLY]: 'Weekly',
  [CALENDAR_EVENT_TYPES.EXCEPTION]: 'Exception',
};

export class CalendarEvent {
  DEFAULT_COLOR = 'gary darken-1';
  constructor(id, name, start, end, color, timed, eventType) {
    this.id = id || uuid();
    this.name = `${CALENDAR_EVENT_DISPLAYNAME[eventType]} | ${name}` || '';
    this.start = start || new Date(`${this.getTodayDate()}T00:00:00`);
    this.end = end || new Date(`${this.getTodayDate()}T23:59:59`);
    this.color = color || this.DEFAULT_COLOR;
    this.timed = timed || false;
    this.eventType = eventType;
    this.category = eventType;
  }

  getTodayDate() {
    const today = new Date();
    const isoTodayDate = today.toISOString();
    return isoTodayDate.split('T')[0];
  }
}

export const DAY_TO_NUMBER_MAP = {
  sunday: 0,
  monday: 1,
  tuesday: 2,
  wednesday: 3,
  thursday: 4,
  friday: 5,
  saturday: 6,
};

export const enumerateDaysBetweenDates = (startDate, endDate, { startInclusive = false, endInclusive = false }) => {
  const dates = [];
  const sDate = startDate.clone().startOf('day');
  const eDate = endDate.clone().startOf('day');

  if (startInclusive) {
    dates.push(sDate.clone());
  }

  while (sDate.add(1, 'days').diff(eDate) < 0) {
    dates.push(sDate.clone());
  }

  if (endInclusive && sDate !== eDate) {
    dates.push(sDate.clone());
  }

  return dates;
};

export function formatEventDate(date) {
  if (typeof date == 'string') {
    date = new Date(date);
  }
  return moment(date).format(DATE_FORMAT);
}

function getWeeklyEvent({ id, name, value, color, startTime, endTime, date }, timezone) {
  const startDate = formatWeeklyDateTime(date, startTime);
  const endDate = formatWeeklyDateTime(date, endTime);
  return new CalendarEvent(id, `${name} - ${value}`, startDate, endDate, color, true, CALENDAR_EVENT_TYPES.WEEKLY);
}

function replaceDaysByWeekDay(weeklyEvent) {
  return {
    ...weeklyEvent,
    days: weeklyEvent.days.map(dayKey => DAY_TO_NUMBER_MAP[dayKey]),
  };
}

function incrementOneDay(date) {
  let temp = new Date(date);
  temp.setDate(date.getDate() + 1);
  return temp;
}

export function formatWeeklyDateTime(date, time) {
  const timeArray = getTimeArray(time);
  const d = new Date(date);
  d.setHours(...timeArray);
  return d;
}

function getTimeArray(time) {
  let temp = time.split(':').concat('00');
  return temp;
}

export function convertWeeklyEvent(id, weeklyEvent, calendarStartDate, calendarEndDate, timezone) {
  weeklyEvent = replaceDaysByWeekDay(weeklyEvent);
  calendarStartDate = new Date(`${calendarStartDate.date}T00:00:00`);
  calendarEndDate = new Date(`${calendarEndDate.date}T23:59:59`);
  const weeklyEvents = [];
  while (calendarStartDate - calendarEndDate < 0) {
    let currentDay = calendarStartDate.getDay();
    const events = weeklyEvent.days.reduce((acc, weekDay) => {
      if (currentDay == weekDay) {
        const event = getWeeklyEvent(
          {
            ...weeklyEvent,
            id,
            date: new Date(calendarStartDate),
            startTime: weeklyEvent.start,
            endTime: weeklyEvent.end,
          },
          timezone,
        );
        acc.push(event);
      }
      return acc;
    }, []);
    if (events.length) {
      weeklyEvents.push(events);
    }

    calendarStartDate = incrementOneDay(calendarStartDate);
  }

  return weeklyEvents.flat();
}

export function convertDateFixedTimezone(dateString, timezone) {
  return new Date(moment.tz(dateString, timezone).format('YYYY-MM-DD HH:mm:ss'));
}

export function convertEvents(events, eventType = CALENDAR_EVENT_TYPES.EVENT) {
  return Object.keys(events).reduce((acc, eventKey) => {
    const id = eventKey;
    const event = events[eventKey];
    const name = `${event.name} - ${event.value}`;
    const color = event.color;
    const eO = [];
    event.dates.forEach(eventDate => {
      // const startDate = moment.tz(eventDate.start, timezone).format('YYYY-MM-DD HH:mm:ss');
      // const endDate = moment.tz(eventDate.end, timezone).format('YYYY-MM-DD HH:mm:ss');
      eO.push(new CalendarEvent(id, name, new Date(eventDate.start), new Date(eventDate.end), color, true, eventType));
    });
    acc.push(...eO);
    return acc;
  }, []);
}

function transformExceptionEvent(id, event, selectedYear, timezone) {
  const { start, end } = parseExceptionDate(event.date, selectedYear, timezone);
  return new CalendarEvent(id, event.name, start, end, event.color, true, CALENDAR_EVENT_TYPES.EXCEPTION);
}

export function convertExceptionDate(datetime, timezone) {
  const m = moment.tz(datetime, timezone);
  return new Date(m.year(), m.month(), m.date(), 0, 0, 0);
}

export function parseExceptionDate(date, selectedYear, timezone) {
  const dateString = selectedYear.toString().concat('-', date);

  return {
    start: new Date(`${dateString}T00:00:00`),
    end: new Date(`${dateString}T23:59:59`),
  };
}

export function convertExceptionEvent(exceptionEvents, timezone, calendarStartDate) {
  const sDate = new Date(`${calendarStartDate.date}T00:00:00`);
  const selectedYear = sDate.getFullYear();
  const selectedYears = [selectedYear - 1, selectedYear, selectedYear + 1];
  return selectedYears
    .map(currentYear =>
      Object.keys(exceptionEvents).map(key => {
        return transformExceptionEvent(key, exceptionEvents[key], currentYear, timezone);
      }),
    )
    .flat();
}

export function getScheduleNames(config) {
  const defaultScheduleNames = [...DEFAULT_SCHEDULE_NAMES];
  const scheduleNames = Object.values((config && config.names) || {});
  return defaultScheduleNames.concat(...scheduleNames);
}
