<template>
  <v-card>
    <v-card-title>Execption Form</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form" lazy-validation>
        <v-row dense>
          <v-col cols="12">
            <v-select
              v-model="formData.name"
              :items="scheduleNames"
              :rules="[rules.required]"
              item-text="name"
              item-value="name"
              label="Event name"
              persistent-hint
              single-line
            ></v-select>
          </v-col>
          <v-col cols="12">
            <v-slider label="Value" v-model="formData.value" max="100" min="0" thumb-label></v-slider>
          </v-col>
          <v-col cols="12">
            <v-row dense v-for="(item, index) of formData.dates" :key="index">
              <v-col cols="5">
                <date-time-picker
                  :rules="[rules.required, v => startDateValidator(item, index)]"
                  label="Start Date"
                  v-model="item.start"
                ></date-time-picker>
              </v-col>
              <v-col cols="5">
                <date-time-picker
                  :rules="[rules.required, v => endDateValidator(item, index)]"
                  label="End Date"
                  v-model="item.end"
                ></date-time-picker>
              </v-col>
              <v-col cols="2">
                <tooltip-icon-button
                  :disabled="formData.dates.length <= 1"
                  btnColor="error"
                  btnIcon="mdi-delete"
                  buttonSize="medium"
                  btnTooltip="Remove Event Date"
                  :onClick="() => removeEvent(index)"
                />
              </v-col>
              <v-col cols="12">
                <tooltip-icon-button
                  v-if="index === formData.dates.length - 1"
                  btnColor="nubeGreen"
                  btnIcon="mdi-plus"
                  buttonSize="medium"
                  btnTooltip="Add Event Date"
                  :onClick="() => addNewEvent()"
                />
              </v-col>
            </v-row>
          </v-col>

          <v-col cols="12">
            <p class="v-label text-left">Event Color</p>
            <v-color-picker v-model="formData.color" mode="hexa"></v-color-picker>
          </v-col>
        </v-row>
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn text @click="onCancel">Close</v-btn>
      <v-btn v-if="isEdit" color="error" :loading="isDeleting" text @click="onDeleteItem">Delete</v-btn>
      <v-btn color="primary" :loading="isSubmitting" text @click="onSaveForm">Save</v-btn>
    </v-card-actions>
  </v-card>
</template>
<script>
import _get from 'lodash/get';
import { format } from 'date-fns';
import _cloneDeep from 'lodash/cloneDeep';
import * as calendarUtil from './calendarUtil';
import DateTimePicker from '../../Common/DateTimePicker.vue';
import TooltipIconButton from '../../RubixApps/TooltipIconButton.vue';

export default {
  name: 'execption-event-form',
  props: ['onSave', 'onCancel', 'item', 'isEdit', 'config', 'onDelete'],
  components: { TooltipIconButton, DateTimePicker },
  data: vm => ({
    isDeleting: false,
    valid: false,
    dateFormatted: null,
    isSubmitting: false,
    showMenu: false,
    formData: {
      name: '',
      dates: [
        {
          start: null,
          end: null,
        },
      ],
      color: '',
      value: null,
    },
    rules: {
      required: value => !!value || 'Required.',
      numberRule: v => !isNaN(parseFloat(v)) || 'Required.',
    },
  }),
  mounted() {
    if (this.isEdit && this.item) {
      this.formData = {
        name: this.item.name,
        color: this.item.color,
        value: this.item.value,
        dates: (this.item.dates || []).map(val => ({
          start: new Date(val.start),
          end: new Date(val.end),
        })),
      };
    }
  },
  computed: {
    computedDateFormatted() {
      return this.formatDate(this.date);
    },
    scheduleNames() {
      return calendarUtil.getScheduleNames(this.config);
    },
  },
  watch: {
    date(val) {
      this.dateFormatted = this.formatDate(this.date);
    },
  },
  methods: {
    startDateValidator(item, index) {
      const startDate = _get(this.formData, `dates[${index}].start`, undefined);
      const endDate = _get(this.formData, `dates[${index}].end`, undefined);
      if (!endDate || !startDate) {
        return true;
      }
      const sDate = new Date(startDate);
      const eDate = new Date(endDate);
      return sDate - eDate > 0 ? 'Start date should be less than End date' : true;
    },
    endDateValidator(item, index) {
      const startDate = _get(this.formData, `dates[${index}].start`, undefined);
      const endDate = _get(this.formData, `dates[${index}].end`, undefined);
      if (!endDate || !startDate) {
        return true;
      }
      const sDate = new Date(startDate);
      const eDate = new Date(endDate);
      return sDate - eDate > 0 ? 'End date should be more than Start date' : true;
    },
    getDate(date) {
      const currentDate = new Date();
      const year = currentDate.getFullYear();
      const d = date.concat('-', year);
      return new Date(d).toISOString().substr(0, 10);
    },
    getFormattedDate(date) {
      return format(new Date(`${date}T00:00:00`), 'MM-dd');
    },
    addNewEvent() {
      this.formData.dates.push({
        start: null,
        end: null,
      });
    },
    removeEvent(number) {
      this.formData.dates = this.formData.dates.filter((_, index) => index != number);
    },
    formatData(formData) {
      const payload = _cloneDeep(formData);
      return {
        ...payload,
        dates: payload.dates.map(val => ({
          start: calendarUtil.formatEventDate(val.start),
          end: calendarUtil.formatEventDate(val.end),
        })),
      };
    },
    formatDate(date) {
      if (!date) return null;

      const [year, month, day] = date.split('-');
      return `${month}-${day}`;
    },
    parseDate(date) {
      if (!date) return null;

      const [month, day] = date.split('-');
      const currentYear = new Date().getFullYear();
      return `${currentYear}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`;
    },
    onSaveForm() {
      if (!this.$refs.form.validate()) {
        return;
      }
      this.formData.date = this.dateFormatted;
      this.isSubmitting = true;
      this.onSave(this.formatData(this.formData), this.item ? this.item.id : null)
        .then(val => {
          this.onCancel();
        })
        .catch(() => {})
        .finally(() => {
          this.isSubmitting = false;
        });
    },
    onDeleteItem() {
      this.isDeleting = true;
      this.onDelete(this.item.id)
        .then(() => {
          this.$toasted.show('Successfully deleted');
          this.onCancel();
        })
        .catch(err => {})
        .finally(() => {
          this.isDeleting = false;
        });
    },
  },
};
</script>
