<template>
  <v-card outlined class="ma-2" :loading="isLoadingCalendar">
    <v-toolbar flat dense>
      <v-toolbar-title><span class="subtitle-1">Calendar Schedule</span></v-toolbar-title>
      <v-spacer></v-spacer>
      <span class="caption pr-3">{{ timezone }}</span>
      <span>
        <tooltip-button
          title="Event"
          color="nubeAqua"
          btnIcon="mdi-plus"
          className="mr-1 btn-height-22 white-text"
          helpText="An event is a single ON event that will put the scheduled event into a TRUE mode."
          :onClick="() => toggleDialog(dialogNames.createEventDialog)"
        ></tooltip-button>
        <tooltip-button
          title="Weekly"
          color="nubeAqua"
          btnIcon="mdi-plus"
          className="mr-1 btn-height-22 white-text"
          helpText="A weekly event is a 7 day repeating time schedule, when the time is within the scheduled slot then the event is set to TRUE mode."
          :onClick="() => toggleDialog(dialogNames.createWeeklyEventDialog)"
        ></tooltip-button>
        <tooltip-button
          title="Exception"
          color="nubeAqua"
          btnIcon="mdi-plus"
          className="mr-1 btn-height-22 white-text"
          helpText="An exception event is a single OFF event that will put the scheduled event into a FALSE mode."
          :onClick="() => toggleDialog(dialogNames.createExceptionDialog)"
        ></tooltip-button>
        <calendar-extra-panel
          :scheduleNames="config.names"
          :timezone="config.timezone"
          :onSubmit="onSaveConfig"
        ></calendar-extra-panel>
      </span>
    </v-toolbar>
    <v-sheet tile height="54" class="d-flex">
      <v-btn icon class="ma-2" @click="$refs.calendar.prev()">
        <v-icon>mdi-chevron-left</v-icon>
      </v-btn>
      <v-select v-model="type" :items="types" dense outlined hide-details class="ma-2" label="Type"></v-select>
      <v-spacer></v-spacer>
      <v-select
        v-model="weekday"
        :items="weekdays"
        dense
        outlined
        hide-details
        label="Weekdays"
        class="ma-2"
      ></v-select>
      <v-btn icon class="ma-2" @click="$refs.calendar.next()">
        <v-icon>mdi-chevron-right</v-icon>
      </v-btn>
    </v-sheet>
    <v-sheet height="500">
      <v-calendar
        ref="calendar"
        :type="type"
        v-model="value"
        @click:event="onEventClick"
        :events="events"
        :weekdays="weekday"
        @change="getEvents"
        :event-overlap-mode="mode"
        :event-overlap-threshold="30"
        :event-color="getEventColor"
      ></v-calendar>
    </v-sheet>
    <v-dialog v-model="dialog[dialogNames.createEventDialog]" persistent max-width="500px">
      <event-form
        v-if="dialog[dialogNames.createEventDialog]"
        :isEdit="false"
        :config="config"
        :timezone="timezone"
        :onCancel="() => closeDialog(dialogNames.createEventDialog)"
        :onSave="data => create(data, formTypes.events)"
      ></event-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.createWeeklyEventDialog]" persistent max-width="500px">
      <weekly-event-form
        v-if="dialog[dialogNames.createWeeklyEventDialog]"
        :isEdit="false"
        :config="config"
        :timezone="timezone"
        :onCancel="() => closeDialog(dialogNames.createWeeklyEventDialog)"
        :onSave="data => create(data, formTypes.weekly)"
      ></weekly-event-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.createExceptionDialog]" persistent max-width="500px">
      <exception-event-form
        v-if="dialog[dialogNames.createExceptionDialog]"
        :isEdit="false"
        :config="config"
        :timezone="timezone"
        :onCancel="() => closeDialog(dialogNames.createExceptionDialog)"
        :onSave="data => create(data, formTypes.exception)"
      ></exception-event-form>
    </v-dialog>

    <v-dialog v-model="dialog[dialogNames.updateEventDialog]" persistent max-width="500px">
      <event-form
        v-if="dialog[dialogNames.updateEventDialog]"
        :isEdit="true"
        :config="config"
        :item="selectedItem"
        :onDelete="id => onDelete(formTypes.events, id)"
        :timezone="timezone"
        :onCancel="() => closeDialog(dialogNames.updateEventDialog)"
        :onSave="(data, id) => update(data, formTypes.events, id)"
      ></event-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.updateWeeklyEventDialog]" persistent max-width="500px">
      <weekly-event-form
        v-if="dialog[dialogNames.updateWeeklyEventDialog]"
        :isEdit="true"
        :config="config"
        :timezone="timezone"
        :item="selectedItem"
        :onDelete="id => onDelete(formTypes.weekly, id)"
        :onCancel="() => closeDialog(dialogNames.updateWeeklyEventDialog)"
        :onSave="(data, id) => update(data, formTypes.weekly, id)"
      ></weekly-event-form>
    </v-dialog>
    <v-dialog v-model="dialog[dialogNames.updateExceptionDialog]" persistent max-width="500px">
      <exception-event-form
        v-if="dialog[dialogNames.updateExceptionDialog]"
        :isEdit="true"
        :config="config"
        :item="selectedItem"
        :timezone="timezone"
        :onDelete="id => onDelete(formTypes.exception, id)"
        :onCancel="() => closeDialog(dialogNames.updateExceptionDialog)"
        :onSave="(data, id) => update(data, formTypes.exception, id)"
      ></exception-event-form>
    </v-dialog>
  </v-card>
</template>
<script>
import { generate as uuid } from 'short-uuid';
import _get from 'lodash/get';
import EventForm from './EventForm.vue';
import _cloneDeep from 'lodash/cloneDeep';
import WeeklyEventForm from './WeeklyForm.vue';
import * as calendarUtil from './calendarUtil';
import * as timezoneUtil from '../../../utils/timezone';
import CalendarExtraPanel from './CalendarExtraPanel.vue';
import ExceptionEventForm from './ExceptionEventForm.vue';
import TooltipButton from '../../Common/TooltipButton.vue';

const dialogNames = {
  createEventDialog: 'createEventDialog',
  createWeeklyEventDialog: 'createWeeklyEventDialog',
  createExceptionDialog: 'createExceptionDialog',
  updateEventDialog: 'updateEventDialog',
  updateWeeklyEventDialog: 'updateWeeklyEventDialog',
  updateExceptionDialog: 'updateExceptionDialog',
};
const eventTypes = calendarUtil.CALENDAR_EVENT_TYPES;

export default {
  name: 'calendar-view',
  data: () => ({
    type: 'week',
    rules: { required: value => !!value || 'Required.' },
    types: ['month', 'week', 'day'],
    mode: 'stack',
    selectedItem: null,
    weekday: [0, 1, 2, 3, 4, 5, 6],
    weekdays: [
      { text: 'Sun - Sat', value: [0, 1, 2, 3, 4, 5, 6] },
      { text: 'Mon - Sun', value: [1, 2, 3, 4, 5, 6, 0] },
      { text: 'Mon - Fri', value: [1, 2, 3, 4, 5] },
    ],
    value: '',
    events: [],
    dialogNames: { ...dialogNames },
    dialog: {
      [dialogNames.createEventDialog]: false,
      [dialogNames.createWeeklyEventDialog]: false,
      [dialogNames.createExceptionDialog]: false,
      [dialogNames.updateEventDialog]: false,
      [dialogNames.updateWeeklyEventDialog]: false,
      [dialogNames.updateExceptionDialog]: false,
    },
    formTypes: {
      events: 'events',
      exception: 'exception',
      weekly: 'weekly',
    },
  }),
  computed: {
    timezone() {
      return this.config.timezone;
    },
  },
  props: ['data', 'updateData', 'isLoadingCalendar', 'config'],
  components: { EventForm, WeeklyEventForm, CalendarExtraPanel, ExceptionEventForm, TooltipButton },
  methods: {
    create(formData, formType) {
      const newData = _cloneDeep(this.data);
      if (!newData[formType]) {
        newData[formType] = {};
      }
      newData[formType][uuid()] = {
        ...formData,
      };
      return this.updateData(newData, this.config);
    },
    update(formData, formType, id) {
      const newData = _cloneDeep(this.data);
      newData[formType][id] = {
        ...formData,
      };
      return this.updateData(newData, this.config);
    },
    onDelete(formType, id) {
      const newData = _cloneDeep(this.data);
      delete newData[formType][id];
      return this.updateData(newData, this.config);
    },
    onSaveConfig(newConfig) {
      return this.updateData(this.data, newConfig);
    },
    event({ name, start, end, color, timed }) {
      return {
        name,
        start,
        end,
        color,
        timed,
      };
    },
    getEvents({ start, end }) {
      this.events = this.transformDataToView(this.data, { calendarStartDate: start, calendarEndDate: end });
      return this.events;
    },
    getEventColor(event) {
      return event.color;
    },
    toggleDialog(dialogName, item) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];

      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    rnd(a, b) {
      return Math.floor((b - a + 1) * Math.random()) + a;
    },
    transformDataToView(data, { calendarStartDate, calendarEndDate }) {
      const events = data.events || {};
      const weekly = data.weekly || {};
      const exceptionEvents = data.exception || {};

      const eventList = calendarUtil.convertEvents(events);

      const weeklyList = Object.keys(weekly)
        .map(weeklyEventKey =>
          calendarUtil.convertWeeklyEvent(
            weeklyEventKey,
            weekly[weeklyEventKey],
            calendarStartDate,
            calendarEndDate,
            this.timezone,
          ),
        )
        .flat();
      const exceptionList = calendarUtil.convertEvents(exceptionEvents, eventTypes.EXCEPTION);
      return [...eventList, ...weeklyList, ...exceptionList];
    },
    getEventDataObject(eventType, id) {
      const event = _get(this.data, `${eventType}[${id}]`);
      return {
        ...event,
        id,
      };
    },
    onEventClick(e) {
      const event = e.event;
      const eventId = event.id;
      switch (event.eventType) {
        case eventTypes.EVENT:
          this.toggleDialog(dialogNames.updateEventDialog, this.getEventDataObject(eventTypes.EVENT, eventId));
          break;
        case eventTypes.WEEKLY:
          this.toggleDialog(dialogNames.updateWeeklyEventDialog, this.getEventDataObject(eventTypes.WEEKLY, eventId));
          break;
        case eventTypes.EXCEPTION:
          this.toggleDialog(dialogNames.updateExceptionDialog, this.getEventDataObject(eventTypes.EXCEPTION, eventId));
          break;
      }
    },
  },
};
</script>
